<script>
export default {
  i18n: {
    'zh-CN': {
      errorInfo: '',
      errorText: '',
      pullRefresh: '',
      finishedText: '',
    },
    'en-US': {
      errorInfo: 'Error Info',
      errorText: 'Request failed. Click to reload',
      pullRefresh: 'PullRefresh',
      finishedText: 'Finished',
    },
  },

  data() {
    return {
      cellValue:'',
      value_down1: false,
      option1: [
        { name: '1' },
        { name: '2' },
        { name: '3', subname: '' }
      ],
      result_machine:["",'','RFID',''],
      machine_list:["",'','RFID',''],
      show_shenhe:false,
      show_tk:false,
      message_tk:'',
      tk_number:'',
      message_goods:'',
      list:[],
      finished:false,
      loading:false,
      active:0,
      active_goods:0,
      searchGoods:'',
      addGoodsBoolean:false,
      addGoodsBoolean_s:false,
      realValue:0,
      imageURL: 'https://img.yzcdn.cn/vant/ipad.jpeg',
      show_worksheet:false,
      show_worksheet_kf:false,
      activeNames_type:['0'],
      radio:0,
      message:'',
      fileList:[],
      orderArr:[
        {
        "treatplanid": "16",
        "desc": "",
        "intro": ""
        },
        {
        "treatplanid": "15",
        "desc": "",
        "intro": ""
        },
        {
        "treatplanid": "12",
        "desc": "",
        "intro": "\"\""
        },
         {
        "treatplanid": "14",
        "desc": "",
        "intro": ""
        },
         {
        "treatplanid": "17",
        "desc": "",
        "intro": ""
        },
         {
        "treatplanid": "18",
        "desc": "",
        "intro": ""
        }
         
      ],
      treatplanid_16:false,
      treatplanid_15:false,
      treatplanid_12:false,
      treatplanid_14:false,
      treatplanid_17:false,
      treatplanid_18:false,
    };
  },

  methods: {
    onSelect(item){
      this.value_down1 = false;
      this.cellValue = item.name;
      this.$toast(item.name);
    },
    finishFn15(){
      this.$toast('');
    },
    finishFn(){
      this.$toast('');
    },
    onLoad() {
      // 
      // setTimeout  ajax 
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }

        // 
        this.loading = false;

        // 
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
  
    addGoods(){
       this.addGoodsBoolean = true;
    },
    setpFn(){
      //alert(this.orderArr[this.radio].treatplanid)
      let treatplanid = this.orderArr[this.radio].treatplanid;
      if(treatplanid == 16){
        this.treatplanid_16 = true;
        return
      }
       if(treatplanid == 15){
        this.treatplanid_15 = true;
        return
      }
      if(treatplanid == 12){
        this.treatplanid_12 = true;
        return
      }
      if(treatplanid == 14){
        this.treatplanid_14 = true;
        return
      }
       if(treatplanid == 17){
        this.treatplanid_17 = true;
        return
      }
      if(treatplanid == 18){
        this.treatplanid_18 = true;
        return
      }

    },
    clickFn(index){
      var that = this;
      this.radio = index;
      setTimeout(function(){
        that.activeNames_type = ['0']
      },150)
      
    }
  }
};
</script>

